
section {
  margin: auto;
	padding-top: 100px;
	display: grid;
	width: 1400px;
	grid-template-areas:
	"tabbar tabbar tabbar logout"
	"textarea textarea textarea filelist"
	" . . buttons ."
	;
	grid-template-columns: 3fr 3fr 3fr 1fr;
	grid-template-rows: auto 500px 30px;
}
textarea {
	grid-area: textarea;
	resize: none;
	border: 1px solid gray;
}
.buttonlist {
	grid-area: buttons;
	justify-self: end;
